import Navigation from './components/Navigation';
import Geocultures from './components/Geocultures';
import About from './components/About';
import Cronology from './components/Cronology';
import LandingPage from './components/LandingPage';
import { Route, Switch } from 'react-router-dom';
import './App.css';


function App() {
  return (
    <>
      <Navigation />
      <div className="App">
        <Switch>
          <Route path='/markets' component={LandingPage}/>  
          <Route path='/cronology' component={Cronology}/>  
          <Route path='/geocultures' component={Geocultures}/>  
          <Route path='/about' component={About}/>
          <Route path='/' component={LandingPage}/>
        </Switch>
      </div>
    </>
  );
}

export default App;
